.missionvision-section {
  margin-top: 50px;
  padding: 20px;
  /* background: linear-gradient(135deg, #f8fafc 0%, #eef2ff 100%); */
}

.mission-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
  max-width: 1100px;
  margin: 0 auto;
}

.mission-card {
  background: #ffffff;
  padding: 30px 25px 40px; /* reduced top padding */
  border-radius: 16px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.08);
  text-align: center;
  transition: all 0.4s ease-in-out;
  position: relative;
  overflow: hidden;
}

.mission-card::before {
  content: "";
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, rgba(59,130,246,0.15), transparent 70%);
  transform: rotate(25deg);
  transition: opacity 0.5s;
  opacity: 0;
  z-index: 0;
}

.mission-card:hover::before {
  opacity: 1;
}

.mission-card:hover {
  transform: translateY(-10px) scale(1.02);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.12);
}

.section-title {
  font-size: 1.8rem;
  font-weight: 700;
  color: #0115ae;
  margin-bottom: 12px; /* reduced from 20px */
  margin-top: -10px;   /* pushes heading a little up */
  position: relative;
  z-index: 1;
}

.mission-card p {
  font-size: 1rem;
  line-height: 1.8;
  color: #475569;
  z-index: 1;
  position: relative;
}

/* Animation */
.fade-in-up {
  opacity: 0;
  transform: translateY(30px);
  animation: fadeInUp 1s ease forwards;
}

@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ==========================
   RESPONSIVE DESIGN
========================== */

/* Large tablets & small laptops */
@media (max-width: 1200px) {
  .mission-container {
    max-width: 95%;
    gap: 30px;
  }

  .mission-card {
    padding: 35px 25px;
  }
}

/* Tablets */
@media (max-width: 992px) {
  .mission-container {
    grid-template-columns: 1fr;
    gap: 25px;
  }

  .section-title {
    font-size: 1.6rem;
  }

  .mission-card {
    padding: 30px 20px;
  }
}

/* Mobile */
@media (max-width: 768px) {
  .section-title {
    font-size: 1.4rem;
  }

  .mission-card p {
    font-size: 0.95rem;
    line-height: 1.6;
  }
}

/* Small Mobile */
@media (max-width: 480px) {
  .missionvision-section {
    margin-top: 30px;
    padding: 10px;
  }

  .section-title {
    font-size: 1.25rem;
  }

  .mission-card {
    padding: 20px 15px;
    border-radius: 12px;
  }

  .mission-card p {
    font-size: 0.9rem;
  }
}
